<template>
	<div id="goods">
		<header>
			<div class="headerMain">
				<div @click='$router.back()'>
				<i class="icon iconfont">&#xe679;</i>	
				</div>
				<div @click='$router.push("/cart")'>
					<i class="icon iconfont">&#xe6af;</i>	
				</div>
			</div>	
		</header>
		<div class="swiperBox">	
			<van-swipe @change="onChange"  :autoplay=3000>
			 	<van-swipe-item v-for='(val,i) in goodsData.swiperList' :key='i' @click='showImg(goodsData.swiperList,i)'>
			 		<img :src="val" alt="">
			 	</van-swipe-item>
				<div class="custom-indicator" slot="indicator">
				    {{ current + 1 }}/{{goodsData.swiperList.length}}
				</div>
			</van-swipe>
		</div>
		<div class="goods-info">
			<p class='price'>
				{{goods.price}}
			</p>
			<p class="title">
				{{goods.name}}
			</p>
		</div>
		<div class="select" @click="showPopup">
			
			<div class='spec'>
				<span :class="{'on':item == goods.spec}" v-for="(item,index) in goodsData.spec" :key="index">{{item}}
				</span>
			</div>
			<i class='iconfont icon-right'></i>
		</div>
		<div class="spec">
		</div>
		<div class="commentBox">
			<div class="title">
				<span> ({{goodsData.commCount}})</span>
				<span @click='$router.push("/comments")'><i class='iconfont icon-right'></i></span>
			</div>
			<div class="comment">
				<div class="user">
					<img :src="goodsData.comment[0].face" alt="">
					{{goodsData.comment[0].username}}
					<span class='createTime'>{{goodsData.comment[0].date}}</span>
				</div>
				<div class="content">
					{{goodsData.comment[0].content}}
				</div>
			</div>
		</div>
		<van-divider :style="{ color: '#888', borderColor: '#999', padding: '0 4%' }">
  			
		</van-divider>
		<div class='goodsDesc' v-html='goodsData.descriptionStr'></div>
		<van-divider :style="{ color: '#888', borderColor: '#999', padding: '0 4%' }">
  			
		</van-divider>
		<van-popup v-model="showSpec" closeable :duration=0.2 position='bottom'  :style="{ height: '63%' }">
			<div class="pop-box" v-if='goodsData.swiperList[0]'>
				<img :src="goodsData.swiperList[0]" alt="" @click='showImg(goodsData.swiperList[0])'>
				<div class="pop-desc">
					<p class='price'>
						{{goods.price}}
					</p>
					<p class='select-spec'>
						<span></span>{{goods.spec}} {{goods.number}}
					</p>
				</div>
			</div>
			<div class="place"></div>
			<van-panel title="" >
			  <div>
			  	<van-radio-group v-model="goods.spec">

				    <van-radio :class="{'on':item == goods.spec}" v-for="(item,index) in goodsData.spec" :key="index" :name='item'>{{item}}</van-radio>
				</van-radio-group>
			  </div>
			</van-panel>
			<div class='count'>
			  	<p></p>
			  	<van-stepper v-model="goods.number" integer @plus='addCount' @minus='subCount' @change='changeCount'/>
			</div>
			<div>
				<van-submit-bar
				  :price="total"
				  button-text=""
				  @submit='buy'
				/>
				
			</div>
		</van-popup>
		<van-image-preview
		  v-model="showBigImg"
		  :images="imgs"
		  :start-position='imgIndex'
		>
		</van-image-preview>
		<van-goods-action>
		  	<van-goods-action-icon icon="chat-o" text="" />
		  	<van-goods-action-icon v-if='!isCollection' icon="star-o" text="" @click='collection'  />
		  	<van-goods-action-icon v-else icon="star" text="" @click='collection'/>
		  	<van-goods-action-button color="#be99ff" type="warning" text="" @click='joinCart'/>
		  	<van-goods-action-button color="#7232dd" type="danger" text="" @click='buy'/>
		</van-goods-action>
	</div>
</template>
<script>
	import goodsData from '@/assets/json/goods.json'
	export default{
		name:'goods',
		data(){
			return{
				current: 0,
				total:0,
				showSpec:false,
				showBigImg:false,
				imgIndex:0,
				isCollection:false,
				imgs:[],
				goods:{
					goodsId:'',
					name:'',
					price:'',
					number:1,
					spec:'XS',
					img:''
				},
				goodsData: {
					swiperList: [],
					spec: [],
					comment: [{
						face: "",
						content: "",
						username: ""
					}],
					descriptionStr: ""
				},
			}
		},
		methods:{
			onChange(index) {
		      this.current = index;
		    },
		    loadData(){
		    	this.goodsData=goodsData.data;
		    },
		    showPopup() {
		      this.showSpec = true;
		    },
		    addCount(){
		    	this.total+=this.goods.price*this.goods.number*100;

		    },
		    subCount(){
		    	this.total-=this.goods.price*this.goods.number*100;
		    },
		    changeCount(num){
		    	this.total=this.goods.price*num*100;
		    },
		    showImg(img,index){
		    	if((typeof img)=='object'){
		   			this.imgs=img;
		    		this.imgIndex=index;
		    	}
		    	else{
		    		this.imgs=[img];
		    		this.imgIndex=0;
		    	}
		    	this.showBigImg=true;
		    	
		    },
		    onSubmit(){
				this.$router.push('/confirmOrder');
		    },
		    buy(){
		    	localStorage.setItem('confirmOrder',JSON.stringify([this.goods]));
				this.$router.push('/confirmOrder');
		    },
		    joinCart(){
		    	// 
		    	let list =localStorage.getItem('cart_goodsList');
		    	// 
		    	if(list){
		    		// 
					let isExist = false;
					let noChange=false;
					let changeList=JSON.parse(list);
					changeList.forEach(goods => {
						if(goods.goodsId == this.goods.goodsId && goods.spec == this.goods.spec){
							if(goods.number == this.goods.number){
								noChange=true;					
							}
							//
							goods.number = this.goods.number;
							isExist = true;
						}
					})
		
					if(noChange){
						this.$toast({
							type:'fail',
							message:'~',
							className:'van-toast-1'
						});
						return;
					}
					//  && 
					if(isExist){
						localStorage.setItem('cart_goodsList',JSON.stringify(changeList));
						this.$toast({
							type:'success',
							message:'\n~',
							className:'van-toast-1'
						});
					}
					//   || 
					else{
						let goodsList=JSON.parse(list);
						goodsList.push(this.goods);
						// 
						localStorage.setItem('cart_goodsList',JSON.stringify(goodsList));
						this.$toast({
							type:'success',
							message:'~~',
							className:'van-toast-1'
						});
					}
					
		    	}	
		    	//
		    	else{
		    		let goodsList = [];
		    		goodsList.push(this.goods);
		    		localStorage.setItem('cart_goodsList',JSON.stringify(goodsList));
		    		this.$toast({
						type:'success',
						message:'~~',
						className:'van-toast-1'
					});
		    	}

			
		    },
		    collection(){
		    	let list=localStorage.getItem('collectionList');
		    	let isExist = false;
		    	if(list){
		    		let collectionList=JSON.parse(list);
		    		collectionList.forEach((goods,index)=>{
		    			if(goods.goodsId == this.goods.goodsId && goods.spec == this.goods.spec){
		    				isExist=true;
		    				collectionList.splice(index,1);
		    				localStorage.setItem('collectionList',JSON.stringify(collectionList));
		    			}

		    		})
		    		if(isExist){
		    			this.isCollection=false;
		    			this.$toast({
							type:'success',
							message:'~~',
							className:'van-toast-1'
						});
		    		}else{
		    			collectionList.push(this.goods);
			    		this.isCollection=true;
			    		localStorage.setItem('collectionList',JSON.stringify(collectionList));
			    		this.$toast({
							type:'success',
							message:'~~',
							className:'van-toast-1'
						});
		    		}
		    	}
		    	else{
		    		list=[];
		    		list.push(this.goods);
		    		this.isCollection=true;
		    		localStorage.setItem('collectionList',JSON.stringify(list));
		    		this.$toast({
						type:'success',
						message:'~~',
						className:'van-toast-1'
					});
		    	}
		    	
		    }
		},
		activated(){
			this.current=0;
			this.loadData();
			let goodsImg=sessionStorage.getItem('goodsImg');
			this.goodsData.swiperList[0]=goodsImg;
			let name=sessionStorage.getItem('goodsTitle');
			let price=sessionStorage.getItem('goodsPrice');
			let goodsId=sessionStorage.getItem('goodsId');
			this.goods.name=name;
			this.goods.price=price;
			this.goods.goodsId=goodsId;
			this.goods.img=goodsImg;
			this.isCollection=false;
			this.total=parseInt(price*this.goods.number)*100; 
			let list=localStorage.getItem('collectionList');
			if(list){
		    	let collectionList=JSON.parse(list);
			    collectionList.forEach((goods,index)=>{
				    if(goods.goodsId === this.goods.goodsId){	
				    	this.isCollection=true;
				    }
			    })
		    }
		},
		deactivated(){
			
		},
		// beforeRouteEnteractivated
		beforeRouteEnter(to,from,next){
			if(from.name==='cart'){
				let spec=sessionStorage.getItem('goodsSpec');
				let number=sessionStorage.getItem('goodsNumber');
				next(vm=>{
					if(spec && number){
						vm.goods.number=number;
						vm.goods.spec=spec;
						sessionStorage.removeItem('goodsSpec');
						sessionStorage.removeItem('goodsNumber');
					}
				
				})
			}		
			else{
				next();
			}	
		},
		beforeRouteLeave(to,from,next){
			if(to.name=='confirmOrder'){
				if(this.showSpec)
					this.showSpec=false;
				next();
				return;
			}
			if(this.showSpec || this.showBigImg){
				this.showSpec=this.showBigImg=false;
				this.$router.go(1);
				return;
			}
			// 
			if(to.name!='comments'){
				this.goods.number=1;
				this.goods.spec='XS';
				next();
			}
			else{
				next();
			}
		}

	}
</script>
<style lang='stylus' scoped>
	#goods
		margin-bottom:1.5rem;
	.van-toast-1 
		width:55vw !important;
		font-size:.32rem !important;
	header
		.headerMain
			position:fixed;
			left:0;
			top:0
			width:94%;
			padding:0 3%;
			display: flex;
			align-items: center;
			justify-content: space-between;
			z-index:666;
			height:1rem;
			div
				display: flex;
				color: #fff;
				background-color: rgba(0, 0, 0, 0.2);
				border-radius: 100%;
				height: .6rem;
				width: .6rem;
				align-items: center;
				justify-content: center;
				padding:.06rem;
				.icon
					font-size:.45rem;
	.swiperBox
		height:100vw;
		.van-swipe
			height:100vw;
			position:relative;
			.van-swipe-item
				img
					width:100%;
					height:100%;
			.custom-indicator
				position:absolute;
				right:.2rem;
				bottom:.2rem;
				color:#fff;
				font-size:.28rem;
				background-color: rgba(0, 0, 0, 0.2);
				border-radius: .4rem;
				padding: .06rem .2rem;
	.van-goods-action
		border-top:1px solid  #eee;
		.van-goods-action-button--warning
			background-color:#f0b46c !important;
		.van-goods-action-button--danger
			background-color:#f06c7a !important;
		.van-goods-action-button--first 
			margin-left:.55rem;
		.van-button
			font-size:.28rem;
	.goods-info
		padding: 0 4%;
		margin: .3rem 0;
		.price
			font-size: 23px;
			font-weight: 600;
			color: #f47925;
			margin: 0;
			margin-bottom: .1rem;
		.title
			font-size: .3rem;
			margin: 0;
			line-height: 1.4;	
	.select
		font-size:.28rem;
		color:#a2a2a2;
		margin:.4rem .3rem;
		display:flex;
		align-items:center;
		position:relative;
		.spec
			font-size:.24rem;
			margin-left.2rem;
			span
				margin:0 .1rem;
				padding:.1rem .11rem;
				background-color:#f6f6f6;
				border:1px solid transparent;
				&.on
					border:1px solid red;
		i.iconfont
			position:absolute;
			right:.1rem;	
	.van-radio-group
		display:flex;
		flex-wrap:wrap;
		padding:.28rem .1rem;
		margin-left:.18rem;
		.van-radio
			font-size:.3rem;
			margin:.14rem .1rem;
			margin-left:.14rem;
			.van-radio__icon
				font-size:.32rem;
	.van-cell__title
		font-size:.3rem;
	.van-popup
		overflow:visible;
	.count
		display:flex;
		align-items:center;
		justify-content:space-between;
		padding:0 4vw;
		margin:.3rem 0;
		p
			margin:0;
			font-size:.3rem;
	.pop-box
		height: 1.8rem;
		display:flex;
		margin-top:-.5rem;
		align-items:center;
		padding:0 4%;
		margin-bottom:.4rem;
		margin-left:.12rem;
		img
			width: 1.8rem;
			height:1.8rem;
		.price
			color: #e4393c;
			font-size:.32rem;
			margin-bottom:.1rem;
			font-weight:bold;
		.select-spec
			font-size:.26rem;
			span
				color:#999;
			
		p 
			margin:0;
	.pop-desc
		margin:.5rem 0 0 .13rem;
	.commentBox
		width: 92%;
		padding:.12rem 4%;
		margin-top:.8rem;
		.title
			font-size:.3rem;
			display:flex;
			align-items:center;
			justify-content:space-between;
			span:nth-of-type(1)
				color:#a2a2a2;
			span:last-child
				color:#17e6a1;
	>>>.van-icon-star:before 
		color:gold;
	.comment
	
		margin-top:.5rem;
		.user
			font-size:.26rem;
			margin-bottom:.2rem;
			color:#999;
			display:flex;
			align-items:center;
			.createTime
				margin-left:.18rem;
				color:#a1a1a1;
			img
				width: .54rem;
				height:.54rem;
				border-radius:50%;
				margin-right:.15rem;
		.content
			font-size:.28rem;
	.van-divider
		font-size:.3rem;
</style>


